body {
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
}

.container {
  text-align: center;
}

.game-wrapper {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}

.title {
  color: #ff3366;
}

.score {
  color: #3399ff;
}

.form {
  margin-top: 20px;
}

.question {
  font-size: 24px;
  margin-bottom: 10px;
}

.input {
  padding: 10px;
  width: 80px;
  border-radius: 5px;
  border: 1px solid #cccccc;
  margin-right: 10px;
}

.button {
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #3399ff;
  border: none;
  color: #ffffff;
  cursor: pointer;
}

.popup {
  background-color: rgba(255, 0, 0, 0.5);
  border-radius: 10px;
  padding: 20px;
  margin-top: 20px;
  animation: fadeInOut 2s ease-in-out;
}

.popup-text {
  color: #ffffff;
}

@keyframes fadeInOut {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
